.graph-modal {
  --transition-time: 0.3s;

  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  overflow-x: hidden;
  overflow-y: auto;
  text-align: center;
  background-color: rgba(0 0 0 / 70%);
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);

  &::before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    height: 100%;
  }

  &__container {
    position: relative;
    display: none;
    vertical-align: middle;
    margin: 50px auto;
    border-radius: 10px;
    padding: 40px;
    width: 100%;
    max-width: 535px;
    text-align: left;
    background-color: var(--color-white);
    cursor: default;

    &--finish {
      padding: 50px 80px 85px;
      text-align: center;
    }

    @include tablet {
      margin: 20px auto;
    }

    @include mobile {
      vertical-align: top;
      padding: 20px;
      width: 90%;

      .form__label {
        font-size: 20px;
      }
    }
  }

  &__close {
    position: absolute;
    right: 30px;
    top: 30px;
    border: none;
    padding: 0;
    width: 20px;
    height: 20px;
    background-color: transparent;
    background-image: url("data:image/svg+xml,%3Csvg version='1.1' id='Capa_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 512.001 512.001' style='enable-background:new 0 0 512.001 512.001;' xml:space='preserve'%3E%3Cg%3E%3Cg%3E%3Cpath d='M284.286,256.002L506.143,34.144c7.811-7.811,7.811-20.475,0-28.285c-7.811-7.81-20.475-7.811-28.285,0L256,227.717 L34.143,5.859c-7.811-7.811-20.475-7.811-28.285,0c-7.81,7.811-7.811,20.475,0,28.285l221.857,221.857L5.858,477.859 c-7.811,7.811-7.811,20.475,0,28.285c3.905,3.905,9.024,5.857,14.143,5.857c5.119,0,10.237-1.952,14.143-5.857L256,284.287 l221.857,221.857c3.905,3.905,9.024,5.857,14.143,5.857s10.237-1.952,14.143-5.857c7.811-7.811,7.811-20.475,0-28.285 L284.286,256.002z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E%0A");
    cursor: pointer;

    @include mobile-s {
      right: 20px;
      top: 20px;
    }
  }

  &__title {
    font-size: 36px;
    line-height: 1.2;
    color: var(--color-green);

    @include mobile {
      font-size: 24px;
    }

    @include mobile-s {
      font-size: 20px;
    }
  }

  &__text {
    font-weight: 600;
    font-size: 24px;

    @include mobile {
      font-size: 20px;
    }
  }
}

.graph-modal.is-open {
  z-index: 1000;
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.graph-modal__container.graph-modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
  transition: opacity var(--transition-time);
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
  transition: transform var(--transition-time), opacity var(--transition-time);
}
